function fadeOnload(delay) {
    let fadeOnloads = document.querySelectorAll('.fade-onload');

    fadeOnloads.forEach((e, i) => {
        setTimeout(() => {
            e.classList.add('faded');
        }, i * delay); // 每個元素延遲0.3秒
    });
}

function fadeOnscroll() {
    let fadeOnscrolls = document.querySelectorAll('.fade-onscroll');
    
    let observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                entry.target.classList.add('faded');
                observer.unobserve(entry.target);
            }
        });
    }, { threshold: 0.1 });

    fadeOnscrolls.forEach(e => {
        observer.observe(e);
    });
}

export default {
    fade(delayOnload) {
        setTimeout(() => {
    
            fadeOnload(delayOnload);
            fadeOnscroll();
            
        }, 0); // 延遲0.0秒
    }
}